
















import { Component, Vue, Prop } from 'vue-property-decorator';
import { Profile, User } from '@/interface/Profile';//@はディレクトリ階層がsrcから始まることを意味する
import SetImage from './SetImage.vue';
import jquery from 'jquery';
import axios from 'axios';

@Component({
  components: {
    SetImage,
  },
})
export default class TopPage extends Vue {
  private characterList: Profile[] = [];
  private user: any[] = [];

  private fetch():Promise<any[]>{
    return new Promise((resolve,reject) => {
      $.ajax({
        type: 'GET',
        url: 'http://localhost:8080/sample/api',
        dataType: 'json',
        success: (res: any) => {
          console.log(res);
          resolve(res);
        },
      });
    });
  }

  private created(): void {
    this.characterList.push(
      {
        name: 'AI',
        age: 17,
        image: require('../../../public/img/Ai.jpg'),
        position:{
           position:'relative',
           top: "100px",
           left: "100px",
           color: "blue"
        },
        count:0
      },
      {
        name: 'Kokoro',
        age: 17,
        image: require('../../../public/img/Kokoro.jpg'),
        position:{
           position:'relative',
           top: "550px",
           left: "220px",
           color: "yellow"
        },
         count:0
      },
      {
        name: 'Maki',
        age: 17,
        image: require('../../../public/img/Maki.jpg'),
        position:{
           position:'relative',
           top: "100px",
           left: "-100px",
           color: "black"
        },
         count:0
      },
      {
        name: 'Ringo',
        age: 17,
        image: require('../../../public/img/Ringo.jpg'),
        position:{
           position:'relative',
           top: "200px",
           left: "-220px",
           color: "pink"
        },
         count:0
      },
    );
    Promise.all([
      this.fetch()
      ]).then((users: any) => {
        console.log(users);
      this.user.push(users[0].name);
    });
  }

}
